/* Base Banner Styles */
.banner {
  position: relative;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #1551f6;
  overflow: hidden;
  margin: 0;
  padding: 0;
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.hidden {
  opacity: 0;
  transform: scale(1);
  pointer-events: none;
}

.scrolling-text-container {
  position: absolute;
  top: 30%; /* Adjust based on design preference */
  left: 0;
  width: 100%;
  height: 30%;
  transform: translateY(-50%);
  z-index: 1;
  animation: scroll-text 20s linear infinite;
}

@keyframes scroll-text {
  from {
    transform: translateX(0) translateY(-50%);
  }
  to {
    transform: translateX(-50%) translateY(-50%);
  }
}

.static-image-container {
  position: absolute;
  top: 50%; /* Adjust to position it in the middle */
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  transition: transform 0.3s ease-out;
}

.static-image-container img {
  width: 70vw; /* Slightly smaller for desktops */
  height: auto;
  max-width: 100%;
  object-fit: contain; /* Keep aspect ratio */
}

/* Scroll Down Indicator Styles */
.scroll-down-indicator {
  position: absolute;
  bottom: 20px; /* Adjust as necessary */
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  z-index: 3;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.scroll-down-indicator img {
  width: 60px; /* Adjust size if needed */
  height: auto;
  margin-bottom: 10px;
}

.scroll-text {
  font-size: 18px;
  color: white; /* White text color */
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Add responsiveness for various screens */

/* For large desktops */
@media (min-width: 1200px) {
  .static-image-container img {
    width: 50vw;
  }
}

/* For smaller desktops or tablets */
@media (min-width: 768px) and (max-width: 1199px) {
  .static-image-container img {
    width: 60vw;
  }
}

/* Show BannerMobile on mobile screens (max-width: 767px) */
@media (max-width: 767px) {
  .banner {
    display: none; /* Hide the desktop banner */
  }
}
